import { getAuth } from "@clerk/remix/ssr.server";
import { json, LoaderArgs, redirect } from "@remix-run/node";
import { useLoaderData } from "@remix-run/react";
import DashHeader from "~/components/dash-header";
import { getTotalDashboard } from "~/db/dashboard.server";

export async function loader({ request }: LoaderArgs) {
    const { userId } = await getAuth(request);
    
    if(!userId) {
        return redirect("/sign-up");
    }

    const { totalCategories } = await getTotalDashboard(userId);
    return json({ totalCategories });
}

export default function DashboardIndexRoute(){
    const { totalCategories } = useLoaderData<typeof loader>();

    return (
        <div>
            <DashHeader title="Home" />
            <div className="max-w-4xl m-auto mt-6">
                cat: {totalCategories}
            </div>
        </div>
    )
}